.nav {
    width: 100%;
}
.content {
    width: 520px;
    margin: auto;
    background-color: rgb(183, 200, 221);
    border: 1px gray solid;
    border-radius: 20px;
    margin-bottom: 50px;
}
.heng-line {
    width: 100%;
    height: 30px;
    background-color:  rgb(183, 200, 221);
    margin-bottom: 30px;
}
h2 {
    text-align: center;
    padding: 10px 0;
}
.list {
    width: 600px;
}
ul {
    margin: auto;
}
li {
    position: relative;
    width: 420px;
    height: 50px;
    line-height: 50px;
    list-style-type: none;
    margin: 10px 10px;
    border: 1px rgb(161, 153, 153) solid;
    background-color: floralwhite;
}
/* .list :last-child li {
    margin-bottom: 0;
} */
form {
    width: 500px;
    margin: auto;
}
.text {
    width: 413px;
    height: 40px;
    background-color: rgb(209, 206, 206);
    outline: none;
    border: 2px gray solid;
    font-size: 18px;
    
}
.remove {
    position: absolute;
    width: 50px;
    height: 30px;
    margin-top: 10px;
    right: 10px;
    border: none;
    border: 1px darkgray solid;
    outline: rgb(64, 70, 77);
    background-color: rgb(233, 237, 241);
    border-radius: 2px;
}

.footer {
    width: 450px;
    height: 35px;
    margin: 0 50px;
    /* padding-bottom: 20px; */
}
.all {
    margin-left: 55px;
}
.all,.complete,.uncomplete {
    width: 72px;
    height: 30px;
}
/* .active {
    display: block;
} */

.select,.unselect {
    width: 75px;
    height: 30px;
}
.different {
    width: 50px;
    height: 30px;
    margin: 5px 0 20px 50px;
}
.xm {
    position: absolute;
    width: 120px;
    height: 200px;
    top: 50px;
    left: 250px;
}
.kl {
    position: fixed;
    width: 150px;
    height: 150px;
    bottom: 30px;
    left: 50px;
}
.llz1 {
    position: fixed;
    width: 320px;
    height: 160px;
    right: 50px;
    bottom: 25px;
}
.count {
    position: fixed;
    width: 100px;
    height: 100px;
    left: 75px;
    bottom: 173px;
}
.hengline {
    position: fixed;
    width: 100%;
    height: 30px;
    bottom: 0;
    background-color: rgb(183, 200, 221);
}
